import React from 'react';
import { NavLink } from 'react-router-dom';
let Common = (props)=>{
    const {name,imgsrc,visit,btname} = props;
    return(
        <>
 <section id="header" className="d-flex align-items-center">
                <div className="container-fluid nav_bg">
                    <div className="row">
                        <div className="col-10 mx-auto">
                        <div className="row">
                            <div className="col-md-6 mt-5 mt-lg-0 order-2 order-lg-1 d-flex align-items-center flex-column">
                                <h1>
                                {name}
                                <strong className="brand-name"> VRUSH </strong>
                                </h1>
                                <h2 className="my-3">
                                    We are the team of talented developer making websites
                                </h2>
                                <div className="mt-3">
                                    <NavLink to={visit} className="btn-get-started">{btname}</NavLink>
                                </div>
                            </div>
                            <div className="col-lg-6 order-1 order-lg-2 header-img">
                                <img src={imgsrc} className="img-fluid animated" alt="Home Img"/>
                            </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>

        </>
    )
}

export default Common;